import React, { Component } from 'react'
import axios from 'axios';
import './Form.css';
import logo from '../../assets/Logo EFE-Maroc.png';

var rootStyle = {
  height: '100vh',
  minHeight : '100vh'
}

export default class Form extends Component {
  constructor(props) {
    super(props)
    this.state = {
      id: null,
      fullName: '',
      Phone: '',
      Email: '',
    }
  }

  validateEmail(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  validatePhone(phone) {
    var regex = /^\+(?:[0-9] ?){6,14}[0-9]$/;
    return regex.test(phone);
  }

	handleChange = (value) => {
		this.setState({ fullName: value.target.value });
	};

	handleChange1 = (value) => {
		this.setState({ Phone: value.target.value });
	};

	handleChange2 = (value) => {
		this.setState({ Email: value.target.value });
	};

  HundleSubmit = () => {
    if (this.state.fullName == "") {
      alert("Champ nom complet obligatoire");
    } else {
      if (this.state.Phone == "") {
        alert("Champ téléphone obligatoire");
      } else {
        if (this.state.Email == "") {
          alert("Champ email obligatoire");
        } else {
          if(this.validateEmail(this.state.Email)) {
            console.log("this.state.Phone.length")
            console.log(this.state.Phone.length)
            if(this.validatePhone(this.state.Phone) || (this.state.Phone.length == 10 && this.state.Phone[0] == "0")){
              // Send Email and redirect to home
              axios.post("https://backendefe.cliquaprod.com/SendEmail", {Email: this.state.Email, FullName: this.state.fullName });
              this.props.history.push({
                pathname: '/Confirmation',
              });
            } else {
              alert("Téléphone incorrect !!");
            }
          } else {
            alert("Email incorrect !!");
          }
        }
      }
    }
  }

  render() {
    console.log(this.props.location.state.id);
    return (
      <div
        className="Form-Class" 
        style={rootStyle}
      >
        
          <div className="container Form-Content">
            <div className="jumbotron Form-Content">
              <img src={logo} style={{ width: "200px", backgroundColor: 'white' }} />
              <br />
              <br />
              <h3
                style={{
                  backgroundColor: 'rgba(0, 0, 0, .5)',
                  display: 'table', /* keep the background color wrapped tight */
                  margin: '0px auto 0px auto', /* keep the table centered */
                  padding: '5px', 
                }}
              >
                <span>  Veuillez saisir vos informations pour vous envoyer le formulaire de candidature </span>
              </h3>
              <div id="form" className="container">
                      <fieldset>
                          <div className="form-group">
                              <label>Nom et Prénom:</label>
                              <input name="fullName" id="fullName" onChange={this.handleChange} />
                          </div>
                          <div className="form-group">
                              <label>Téléphone:</label>
                              <input name="Phone" id="Phone" onChange={this.handleChange1} />
                          </div>
                          <div className="form-group" style={{ marginBottom: '30px' }}>
                              <label>E-mail:</label>
                              <input name="Email" id="Email" onChange={this.handleChange2} />
                          </div>
                          <button className="outline purple-white" onClick={this.HundleSubmit}> <i className="fas fa-briefcase" style={{ fontSize: '24px', }}></i> &nbsp; Postuler </button>
                      </fieldset>
              </div>
              <br />
              <br />
              <div style={{ color: 'red', fontSize: '15px', width: '50%', margin: 'auto', backgroundColor: 'black' }}>  *** Vos informations seront utilisées exclusivement par EFE-Maroc dans le cadre de ses opérations de formation-Insertion et seront protégés conformément à la réglementation en vigueur </div>
            </div>
          </div>


      </div>
    )
  }

}