import React, { Component } from 'react'
import axios from 'axios';
import './Offers.css';
import SocialMedia from '../SocialMedia/SocialMedia';
import logo from '../../assets/Logo EFE-Maroc.png';

var rootStyle = {
  height: '100vh',
  minHeight : '100vh',
  overflow: "auto"
}

export default class Offers extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {},
    }
  }

  componentDidMount() {
    this.GetDataOffers();
  }

  GetDataOffers = () => {
    axios.post("https://backendefe.cliquaprod.com/getPostes")
    .then(res => {
      console.log(res);
      this.setState({ data: res.data });
    })
    .catch(err => {
      console.log(err);
    });
  }

  HundleOffer(ids){
    console.log(ids);
    this.props.history.push({
      pathname: '/Form',
      state: {
        id: ids,
      }
    });
  }

  render() {

    if (this.state.data) {
      console.log(this.state.data);
    } else {
      console.log(this.state.data.status);
    }

    return (
      <div
        className="Offers-Class" 
        style={rootStyle}
      >
        {this.state.data && this.state.data.status == 1 ?
          <div className="container Offers-Content scrollable" style={{ width: "70%", paddingTop: "10%" }}>
            <br />
            <br />
            <br />
            <br />
            <img src={logo} style={{ width: "200px", backgroundColor: 'white', }} />
            <br />
            <br />
            {
              this.state.data.response.map((item, index) => {
                return (
                  <div className="wrapper" key={index}>
                    <div id="one">  </div>
                    <div id="two">
                      <div style={{ textAlign: 'left', }}>
                        <h3> {item.titre} </h3>
                        <p> {item.description} </p>
                        <div style={{ textAlign: 'right', }}>
                          <i className="fas fa-map-marker" style={{ fontSize: '19px', }}></i> {item.ville} &nbsp;
                          <button className="outlines" onClick={this.HundleOffer = () => {
                            console.log(item.id);
                            this.props.history.push({
                              pathname: '/Form',
                              state: {
                                id: item.id,
                              }
                            });
                          }}> <i className="fas fa-briefcase" style={{ fontSize: '24px', }}></i>  &nbsp; Postuler </button>
                        </div>
                      </div>
                    </div>
                  </div>
                )
              })
            }
            {
              this.state.data.response.map((item, index) => {
                return (
                  <div className="wrapper" key={index}>
                    <div id="one">  </div>
                    <div id="two">
                      <div style={{ textAlign: 'left', }}>
                        <h3> {item.titre} </h3>
                        <p> {item.description} </p>
                        <div style={{ textAlign: 'right', }}>
                          <i className="fas fa-map-marker" style={{ fontSize: '19px', }}></i> {item.ville} &nbsp;
                          <button className="outlines" onClick={this.HundleOffer = () => {
                            console.log(item.id);
                            this.props.history.push({
                              pathname: '/Form',
                              state: {
                                id: item.id,
                              }
                            });
                          }}> <i className="fas fa-briefcase" style={{ fontSize: '24px', }}></i>  &nbsp; Postuler </button>
                        </div>
                      </div>
                    </div>
                  </div>
                )
              })
            }
          </div>
          : 
          <div className="container" style={{ width: "70%", paddingTop: "10%", }}>
            <h1> NO OFFER </h1>
          </div>
          }
          </div>
    )
  }

}